<template>
  <div>
  <h2> 
    Coins: {{coins}} 
  </h2>
  <h3 v-if="gameName">{{gameName}} Game</h3>
  <p v-if="!gameSelected">{{message}}</p>
  <div v-if="gameSelected === false">
    <!-- this "passes down the method" similarly to react -->
    <game-selector @set-game="setGame"/>
  </div>

  <div v-if="gameName === 'Coin'">
    <coin-game :viewable="betSubmitted && !choiceMade" 
    @player-choice="setPlayerChoice"
     />
      <bet-input v-on:set-bet="setBet" v-if="!betSubmitted && gameSelected" 
      :max = "coins" />
  </div>

  <div v-else-if="gameName === 'Dice'">
    <dice-game :viewable="betSubmitted && !choiceMade" 
     @player-choice="setPlayerChoice"/>
       <bet-input v-on:set-bet="setBet" v-if="!betSubmitted && gameSelected"
       :max = "coins"/>
  </div>
  </div>
</template>

<script>
import BetInput from './BetInput.vue';
import CoinGame from './CoinGame.vue'
import DiceGame from './DiceGame.vue';
import GameSelector from './GameSelector.vue';
export default {
  components: {
    CoinGame,
    DiceGame,
    GameSelector,
    BetInput
 },
  data () {
    return  {
      gameName : "",
      gameSelected: false,
      betSubmitted: false,
      bet: null,
      coins: 100,
      answer: null,
      playerSelection: null,
      choiceMade: false,
      message: null
    }
  },
  methods : {
    setGame (gameName) {
      const game = gameName;
      this.gameName = `${game}`
      this.gameSelected = true
    },
    setBet (amount) {
      this.bet = amount;
      if(amount > this.coins) return
      this.coins = this.coins - amount
      this.betSubmitted = true
      this.answer = this.randomSelection()
    },
    randomSelection(){
      if (this.gameName === 'Coin'){
        const choices = ['heads', 'tails']
        return choices[Math.floor(Math.random() * 2)]
      } else {
        return Math.floor(Math.random() * 6) + 1
      }
    },
    setPlayerChoice (choice){
      this.playerSelection = choice
      this.choiceMade = true;
      this.checkForWin()
    },
    checkForWin(){
      if(this.playerSelection === this.answer){
        this.message = `You won! The correct selection was ${this.answer}!`
        if(this.gameName === 'Coin'){
          alert('winning coins')
          this.coins = this.coins + (this.bet * 2)
            this.reset()
        } else {
          this.coins = this.coins + (this.bet * 6)
          this.reset()
        }
      } else if (this.playerSelection !== this.answer){
        this.message = `You lose! The correct answer was ${this.answer}.`
        this.reset()
      }
     
    },
    reset(){
      this.choiceMade = false;
      this.betSubmitted = false;
      this.gameSelected = false;
      this.playerSelection = null;
      this.answer = this.randomSelection()
    }
  }
}
</script>

